@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&family=Luckiest+Guy&display=swap);
:root {
  --primary: #0d131d;
  --card-width: 300px;
  --card-height: 200px;
  font-size: 16px;
}

@media only screen and (max-width: 950px) {
  :root {
    font-size: 8px;
    --card-width: 160px;
    --card-height: 95px;
  }
}

@media only screen and (max-width: 600px) {
  :root {
    font-size: 6px;
    --card-width: 100px;
    --card-height: 80px;
  }
}

body {
  background: #0d131d;
  background: var(--primary);
  color: #0d131d;
  color: var(--primary);
  margin: 0;
  font-family: "Luckiest Guy", cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
  max-height: 100%;
  max-width: 100%;
  border: 0 0 0 0;
  padding: 0 0 0 0;
}
:focus-visible {
  outline: none;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{display:flex;justify-content:center;align-items:center;width:100vw;height:100vh}.themeSwitch{position:absolute;font-size:1.5rem;color:white;top:0.8rem;right:1.5rem;display:flex;justify-content:center;align-items:center;cursor:pointer}.bgname{color:#0076b7;font-size:10rem;margin-left:1rem;margin-right:1rem;margin-top:8.2rem}.bgname.left{transform:translateX(-200%) scale(0);animation:slideFromLeft 0.3s cubic-bezier(0.49, 0.4, 0.66, 1.15) 0.5s forwards;z-index:1}.bgname.right{transform:translateX(200%) scale(0);animation:slideFromRight 0.3s cubic-bezier(0.49, 0.4, 0.66, 1.15) 1s forwards;z-index:1}.card{width:var(--card-width);height:var(--card-height);min-width:80px;border-radius:10px;transform:perspective(1000px) scale3d(1, 1, 1);transition:all ease-out 0.15s;overflow:hidden;cursor:pointer;display:flex;background-color:var(--primary);background-image:url(data:image/png;base64,);background-size:cover;background-position-y:-15px;background-repeat:no-repeat;animation:zoomOut 0.8s ease, pushLeft 0.1s ease 0.7s forwards, pushRight 0.1s ease 1.18s forwards}.card.ready{animation:none}.card .image{position:absolute;width:55px;height:55px;border-radius:50%;top:22.5px;left:40.5px;object-fit:cover;overflow:hidden;transform:scale(0);transition:all ease 0.3s;box-sizing:border-box;padding:1px}.card:hover .image{transform:scale(1)}.card .content{position:absolute;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center;background-image:url(data:image/png;base64,);background-size:cover;background-position-y:-15px;opacity:0;top:0;bottom:0;left:0;right:0;transition:all ease-out 0.1s}.card:hover .content{opacity:1}.header{display:flex;align-items:center;justify-content:center;position:absolute;top:38px}.name{font-family:"Fredoka One", cursive;font-size:1.1rem;margin-left:4rem;opacity:0;transition:all ease-out 0.3s 0.4s}.card:hover .name{opacity:1;transition:all ease-out 0.3s 0.4s}@media only screen and (max-width: 600px){.image,.name{display:none !important}}.card.zoom{transform:perspective(200px) scale3d(1.1, 1.1, 1.1)}.lights{display:flex;justify-content:space-between;position:absolute}.lights.top{width:100vw;top:-2.2rem}.lights.left{height:100vh;flex-direction:column;left:-1.2rem}.lights.right{height:100vh;float:right;flex-direction:column;right:-2.2rem}.lights.bottom{width:100vw;bottom:-2.5rem}.light{width:1rem;height:2rem;box-shadow:-1px 0px 20px 10px gold, -1px 0px 40px 15px gold, -1px 0px 70px 20px gold;top:-100%;opacity:0;transform:scaleY(0);transition:all ease 0.3s}.light:nth-child(1){transition-delay:100ms}.light:nth-child(2){transition-delay:200ms}.light:nth-child(3){transition-delay:300ms}.light:nth-child(4){transition-delay:400ms}.light:nth-child(5){transition-delay:500ms}.light:nth-child(6){transition-delay:600ms}.light:nth-child(7){transition-delay:700ms}.light:nth-child(8){transition-delay:800ms}.light:nth-child(9){transition-delay:900ms}.light:nth-child(10){transition-delay:1000ms}.light:nth-child(11){transition-delay:1100ms}.light:nth-child(12){transition-delay:1200ms}.light:nth-child(13){transition-delay:1300ms}.light:nth-child(14){transition-delay:1400ms}.light:nth-child(15){transition-delay:1500ms}.light:nth-child(16){transition-delay:1600ms}.light:nth-child(17){transition-delay:1700ms}.light:nth-child(18){transition-delay:1800ms}.light:nth-child(19){transition-delay:1900ms}.light:nth-child(20){transition-delay:2000ms}.light:nth-child(21){transition-delay:2100ms}.light:nth-child(22){transition-delay:2200ms}.light:nth-child(23){transition-delay:2300ms}.light:nth-child(24){transition-delay:2400ms}.light:nth-child(25){transition-delay:2500ms}.light:nth-child(26){transition-delay:2600ms}.light:nth-child(27){transition-delay:2700ms}.light:nth-child(28){transition-delay:2800ms}.light:nth-child(29){transition-delay:2900ms}.light:nth-child(30){transition-delay:3000ms}.light:nth-child(31){transition-delay:3100ms}.light:nth-child(32){transition-delay:3200ms}.light:nth-child(33){transition-delay:3300ms}.light:nth-child(34){transition-delay:3400ms}.light:nth-child(35){transition-delay:3500ms}.light:nth-child(36){transition-delay:3600ms}.light:nth-child(37){transition-delay:3700ms}.light:nth-child(38){transition-delay:3800ms}.light:nth-child(39){transition-delay:3900ms}.light:nth-child(40){transition-delay:4000ms}.light:nth-child(41){transition-delay:4100ms}.light:nth-child(42){transition-delay:4200ms}.light:nth-child(43){transition-delay:4300ms}.light:nth-child(44){transition-delay:4400ms}.light:nth-child(45){transition-delay:4500ms}.light:nth-child(46){transition-delay:4600ms}.light:nth-child(47){transition-delay:4700ms}.light:nth-child(48){transition-delay:4800ms}.light:nth-child(49){transition-delay:4900ms}.light:nth-child(50){transition-delay:5000ms}.show .light{opacity:1;transform:scaleY(1)}.skillsContainer{position:absolute;width:100vw;height:100vh;display:flex;justify-content:space-between;align-items:center}.skill{font-size:7rem;color:white;margin:2rem}.pulse{animation:pulse 1s ease-in 0s 5 alternate}@keyframes slideFromLeft{0%{transform:translateX(-200%) scale(1)}90%{transform:translateX(20%) scale(1)}100%{transform:translateX(0) scale(1)}}@keyframes pushLeft{0%{transform:perspective(1000px) scale3d(1, 1, 1) translateX(0)}100%{transform:perspective(1000px) scale3d(1, 1, 1) translateX(50%)}}@keyframes pushRight{0%{transform:perspective(1000px) scale3d(1, 1, 1) translateX(50%)}100%{transform:perspective(1000px) scale3d(1, 1, 1) translateX(0)}}@keyframes slideFromRight{0%{transform:translateX(200%) scale(1)}100%{transform:translateX(0) scale(1)}}@keyframes zoomOut{from{opacity:0;transform:perspective(600px) scale(5) rotateX(150deg)}to{opacity:1;transform:perspective(600px) scale(1) rotateX(0deg)}}

